@import "../node_modules/now-ui-kit/assets/css/bootstrap.min.css";
@import "../node_modules/now-ui-kit/assets/css/now-ui-kit.css";
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@423&display=swap");

body,
button,
input,
optgroup,
select,
textarea {
  font-size: 20px;
  background-color: #f9f9f9;
  font-family: "Josefin Sans", sans-serif;
}
.line-through .form-check-label {
  text-decoration: line-through;
}
.todo-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.alert {
  transition: all 0.5s linear;
}
.alert-secondary {
  color: #000;
  background-color: #fff;
}
.layout {
  font-size: 1.6rem;
  font-weight: bold;
}
.add-todo {
  padding: 0.75rem 1.25rem;
  margin-top: 4rem;
  margin-bottom: 1rem;
}
.add-todo .inline {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.add-todo .form-group {
  width: 94%;
  height: 45px;
}
.slide-right {
  -webkit-animation: slide-right 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-right 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@-webkit-keyframes slide-right {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(100px);
    transform: translateX(100px);
  }
}
@keyframes slide-right {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(100px);
    transform: translateX(100px);
  }
}
.btn-white {
  background-color: #fff;
  color: rgba(249, 99, 50, 0.8);
}
.navbar-nav {
  display: inline-flex !important;
  flex-direction: row;
}
